@import url(https://fonts.googleapis.com/css?family=Oswald:400,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
html, #root, .App {
  height: 100%; }

body {
  margin: 0;
  height: 100%;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #141419; }

a {
  color: yellow; }

a:visited {
  color: #be8000; }

.ErrorMsg {
  background-color: #b35151;
  padding: 20px;
  text-align: center;
  font-size: 18px;
  border-radius: 4px;
  font-weight: 800;
  color: white;
  margin-top: 30px; }

header {
  background-color: black;
  color: yellow; }
  header h1 {
    margin: 0;
    padding: 10px 24px;
    font-family: 'Oswald', sans-serif;
    font-size: 32px;
    text-align: left;
    font-style: italic; }

main {
  padding: 20px;
  padding-bottom: 50px;
  margin: 0 10vw;
  min-height: 770px; }
  main .desc {
    font-size: 18px;
    color: #e7e7e7;
    text-align: center;
    margin: auto; }
  main #req_container {
    position: relative;
    display: inline-flex;
    width: 100%;
    margin: 30px 0px; }
  main #req_input {
    width: 100%;
    padding: 18px;
    font-size: 18px;
    color: #323232;
    border-radius: 5px;
    border: none;
    z-index: 1; }
  main #req_input::-webkit-input-placeholder {
    font-size: 18px;
    opacity: .5;
    font-style: italic; }
  main #req_input::-moz-placeholder {
    font-size: 18px;
    opacity: .5;
    font-style: italic; }
  main #req_input:-ms-input-placeholder {
    font-size: 18px;
    opacity: .5;
    font-style: italic; }
  main #req_input::-ms-input-placeholder {
    font-size: 18px;
    opacity: .5;
    font-style: italic; }
  main #req_input::placeholder {
    font-size: 18px;
    opacity: .5;
    font-style: italic; }
  main #fleshmonk_container {
    float: right; }
  main #req_generate {
    border: none;
    border-radius: 5px;
    background-color: yellow;
    color: black;
    font-size: 26px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
    padding: 10px 20px; }
  main #req_generate:hover {
    cursor: pointer; }
  main #fleshmonk_container:after {
    content: '';
    position: relative;
    top: -161px;
    left: -240px;
    width: 0;
    height: 0;
    border: 80px solid transparent;
    border-top-color: #ffffff;
    border-bottom: 0;
    border-right: 0;
    margin-left: -24.5px;
    margin-bottom: -49px; }
  main #fleshmonk_logo {
    height: 200px;
    width: 200px; }

.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }

.wordList {
  font-family: 'Montserrat', sans-serif;
  margin-top: 200px; }
  .wordList h2 {
    color: white;
    font-size: 32px;
    text-align: center; }
  .wordList .letterContainer {
    padding: 10px 40px; }
    .wordList .letterContainer h3 {
      font-size: 24px;
      color: yellow; }
    .wordList .letterContainer ul {
      list-style: none;
      padding: 0; }
    .wordList .letterContainer li {
      padding: 2px 0px; }
      .wordList .letterContainer li button {
        padding: 10px 15px;
        color: #e7e7e7;
        font-size: 18px;
        border: none;
        background-color: rgba(0, 0, 0, 0.16); }
      .wordList .letterContainer li button:hover {
        cursor: pointer; }

footer {
  width: 100%;
  box-sizing: border-box;
  background-color: #000000;
  padding: 10px 25px;
  color: #cecece;
  text-align: center; }
  footer img {
    display: inline;
    width: 20px;
    height: 20px;
    vertical-align: text-bottom;
    padding-right: 5px; }
  footer p {
    display: inline; }

@media (max-width: 768px) {
  main {
    padding: 20px;
    margin: 0 5vw; } }

@media (max-width: 710px) {
  main {
    padding: 20px;
    margin: 0 10px; }
    main #fleshmonk_logo {
      height: 150px;
      width: 150px; }
    main #fleshmonk_container:after {
      top: -127px;
      left: -162px;
      border-top-color: #ffffff;
      margin-left: -24.5px;
      margin-bottom: -49px; } }

@media (max-width: 425px) {
  main {
    padding: 20px;
    margin: 0 10px; }
    main #fleshmonk_logo {
      height: 125px;
      width: 125px; }
    main #fleshmonk_container:after {
      top: -105px;
      left: -32px;
      border: 40px solid transparent;
      border-left: 0; } }

@media (max-width: 375px) {
  main {
    padding: 20px;
    margin: 0 10px; }
    main #fleshmonk_logo {
      height: 100px;
      width: 100px; }
    main #fleshmonk_container:after {
      display: none; } }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

@media (max-width: 375px) {
  .wordList {
    margin-top: 120px; } }

